:root {
  --tbgblue: #3e64ff;
  --tbglueop1: #738efab4;
  --tbgblueop2: #3ed5ffc1;
  --textlow-1: rgba(59, 59, 59, 0.695);
  --textlow-2: rgba(87, 87, 87, 0.804);
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  /* font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif; */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 17px;
  font-family: "Poppins", Arial, sans-serif;
}

code {
  font-family: 'Nunito', sans-serif, source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Navbar */
.navbar {
  width: 75%;
  position: fixed;
  left: 50%;
  transform: translateX(-50%);
  transition: background-color 0.3s ease;
  z-index: 100;
}

.navbar.scrolled {
  justify-content: center;
  gap: 35.3%;
  width: 100%;
  background-color: rgb(255, 255, 255);
  box-shadow: 1px 1px 10px rgba(0, 0, 0, 0.275);
}

.logo a {
  font-size: 30px;
  font-weight: 800;
  color: black;
  cursor: pointer;
}

.logo a span {
  border-radius: 50%;
  background-color: var(--tbgblue);
  color: beige;
  padding: 2px 12px;
}

.navigationlist,
.navbar {
  list-style: none;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.navitem {
  cursor: pointer;
  padding: 4px .7rem;
  margin: 0px 10px;
  font-weight: 400;
  position: relative;
}

.navitem a {
  color: black
}

.navitem::after {
  content: "";
  position: absolute;
  width: 0%;
  height: 2px;
  left: 8px;
  bottom: 0;
  background-color: var(--tbgblue);
  transition: width .5s;
}

.navitem:hover::after {
  width: 82%;
}

#opennav {
  height: 20px;
  position: relative;
  cursor: pointer;
  display: none;
}

#opennav::before {
  content: "";
  position: absolute;
  height: 2.5px;
  width: 25px;
  background-color: var(--textlow-2);
  top: 0;
  left: 4px;
  transition: all .5s;
}

#opennav::after {
  content: "";
  position: absolute;
  height: 2.5px;
  width: 25px;
  background-color: var(--textlow-2);
  bottom: -0px;
  left: 4px;
  box-shadow: 0 -9px 0px var(--textlow-2);
  transition: all .5s;
}

.navbar .closenav::before {
  transform: translateY(9px) rotate(45deg);
}

.navbar .closenav::after {
  transform: translateY(-8px) rotate(-45deg);
  box-shadow: none !important;
}

/* home */
.home {
  height: 100vh;
  position: relative;
}

.home::before {
  content: "";
  width: 49.5vw;
  height: 100vh;
  position: absolute;
  left: 0;
  z-index: -1;
  opacity: .1;
  background-color: var(--tbgblue);
}

/* center Name box */
.introcenter {
  height: 200px;
  margin: auto;
  padding-top: 35vh;
  text-align: center;
  letter-spacing: 2px;
}

.introcenter h1 {
  font-size: 3.5rem;
}

.introcenter h1,
.introcenter h4,
.introcenter p {
  font-weight: 800;
  margin: 5px;
}

.introcenter h4,
.introcenter span,
.hmovedown i {
  color: var(--tbgblue);
  position: relative;
}

.introcenter p {
  font-size: 32px;
  margin-top: 13px;
  letter-spacing: normal;
}

.introcenter #chnagetext {
  text-decoration: underline;
  padding-left: 10px;
}

/* go Down box */
.hmovedown {
  width: 70px;
  height: 70px;
  position: absolute;
  margin: auto;
  bottom: -300px;
  overflow: hidden;
  position: relative;
  transform: rotate(45deg);
}

.hmovedown div {
  transform: rotate(-45deg);
  width: 70px;
  height: 70px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.hmovedown::after {
  content: "";
  position: absolute;
  width: 113px;
  height: 53px;
  top: -7px;
  opacity: .1;
  transform: rotate(45deg);
  background-color: var(--tbgblue);
}

/* ABout section */
.about {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin-top: 1px;
}

.aboutimg,
.aboutdata {
  max-width: 520px;
}

.aboutimg img {
  width: 100%;
  height: 100%;
}

.aboutdata {
  padding-left: 50px;
  letter-spacing: 1px;
}

.aboutdata h1,
.skillsection h1,
.projectsec h1,
.contactsec h1 {
  font-size: 3rem;
  letter-spacing: 1px;
}

.aboutdata p,
.projectdatas p {
  color: var(--textlow-2);
}

.linkicon {
  padding: 20px 0px;
  text-align: center;
}

.linkicon a {
  margin: 10px;
}

.connect {
  display: flex;
  gap: 10px;
}

.connect p {
  margin: 0;
}

.resume {
  margin-top: 25px;
  padding: 15px 20px;
  border: none;
  border-radius: 25px;
  font-size: 18px;
  color: aliceblue;
  background-color: var(--tbgblue);
  cursor: pointer;
}

.resume:hover {
  transform: scale(1.01);
  box-shadow: 5px 5px 10px rgba(9, 9, 9, 0.478);
  color: black;
}

/* Skills Section */
.skillsection {
  margin-top: 200px;
  text-align: center;
  padding: 0px 10px;
}

.skillbox {
  width: 90%;
  margin: auto;
  padding: 30px 50px;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  justify-content: center;
  gap: 40px;
}

.skill {
  height: 220px;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: rgba(255, 255, 255, 0.1);
  -webkit-box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.15) !important;
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.15) !important;
}

.skill:hover img {
  transform: scale(1.1);
}

.skill:hover {
  background-color: var(--tbglueop1);
  box-shadow: none;
}

/* Project section */
.projectsec {
  width: 80%;
  margin: auto;
  padding: 50px 20px;
  text-align: center;
}

.projectcardbox {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(290px, 1fr));
  justify-content: center;
  gap: 40px;
}

.projectcard {
  cursor: context-menu;
  margin-top: 40px;
  padding: 5px;
  background: rgba(255, 255, 255, 0.1);
  -webkit-box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.15) !important;
  box-shadow: 0 0.2rem 1rem rgba(0, 0, 0, 0.15) !important;
}

.projectimg {
  border: 1px solid rgb(230, 230, 230);
  position: relative;
  overflow: hidden;
  cursor: pointer;
}

.projectimg img {
  width: 100%;
}

.projectimg .demo {
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: aliceblue;
  text-shadow: 2px 2px 5px black;
  background-color: var(--tbgblueop2);
  top: 180px;
  transition: all 1s;
}

.btndemo{
  cursor: pointer;
  padding: 5px 10px;
  border:none;
  background-color: white;
  border-radius: 10px;
  box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.518);
}

.projectimg:hover .demo {
  top: 0;
}

.projectdatas {
  padding: 10px;
}

.projectdatas a {
  color: var(--tbgblue);
  text-decoration: var(--tbgblue) wavy underline;
  cursor: pointer;
}

.projectdatas h3 {
  position: relative;
}

.projectdatas h3::after {
  content: "";
  position: absolute;
  background-color: var(--tbgblue);
  height: 2px;
  bottom: -6px;
  left: 39%;
  width: 70px;
}

/* hire me */
.hirebanner{
  color: white;
  margin: 100px 0px;
  background: url("./iconImages/background9.webp");
}
.hirebox{
  padding: 90px 10px;
  background-color: #3e65ff7a;
}
.hiretext{
  margin: auto;
  padding: 10px;
  width: 95%;
  max-width: 500px;
  text-align: center;
}
.hiretext h2{
  font-size: 40px;
  margin:0px;
}
.hiretext h2 span{
  color: greenyellow;
}
.hiretext button{
  cursor: pointer;
  color: white;
  letter-spacing: .5px;
  border: none;
  text-transform: uppercase;
  margin: 20px;
  font-weight: 600;
  padding: 15px 50px ;
  border-radius: 20px;
  background-color: var(--tbgblue);
  transition: all .2s;
}
.hiretext button:hover{
  box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.513);
}


/* contact */
.contactsec {
  text-align: center;
  margin: 20px 0px;
}

.contact {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.contact img,
.mailbox {
  border: 1px dotted rgba(122, 122, 122, 0.483);
  min-width: 480px;
  max-width: 510px;
  height: 520px;
  background: #f8f9fa !important;
}

.mailbox {
  padding: 50px 0px;
}

.mailbox form {
  margin: auto;
  width: 80%;
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.mailbox input {
  height: 50px;
}

.mailbox input,
.mailbox textarea {
  font-size: 15px;
  padding: 10px;
  width: 100%;
  border: 1px solid rgb(194, 194, 194);
  border-radius: 5px;
  resize: none;
  letter-spacing: 1px;
}

.mailbox #SendMessage {
  /* font-size: 12px; */
  letter-spacing: 2px;
  font-weight: 600;
  border-radius: 25px;
  color: white;
  border: none;
  background-color: var(--tbgblue);
  padding: 15px;
  cursor: pointer;
}

#SendMessage:hover {
  color: black;
  box-shadow: 2px .2rem 1rem var(--tbgblueop2);
}
footer{
  margin-top:70px;
  background-color: black;
  color: white;
  padding: 10px;
  text-align: center;
  text-transform: capitalize;
}
footer span:nth-child(2){
  color: red;
  font-size: 30px;
}
/* media query */
@media screen and (max-width:1040px) {
  .navbar.scrolled {
    gap: 15%;
  }

  .home::before {
    content: "";
    width: 50vw;
  }

  .aboutimg {
    border-radius: 50%;
    overflow: hidden;
    margin: 150px 0px 50px 0px;
  }

  .contact img {
    border: none;
  }
}

@media screen and (max-width:704px) {
  .navbar{padding: 10px 0px;}
  .navbar.scrolled {
    gap: 55%;
  }

  #opennav {
    display: block;
  }
  /* .navbar {
    padding-top: 30px;
  } */
  .navigation {
    display: none;
    position: absolute;
    right: -40px;
    top: 80px;
    padding: 10px 30px 10px 10px;
    background-color: #3e65ff19;
    border-radius: 20px;
    font-size: larger;
    z-index: 10000;
  }

  .navbar.scrolled .navigation {
    right: 20px;
    top: 100px;
  }

  .navigationlist {
    display: block;
  }
}

@media screen and (max-width:576px) {
  .navigation {
    right: -10px;
    padding: 10px 10px 10px 0px;
    font-size: medium;
  }

  .introcenter h1 {
    font-size: 2.7rem;
  }

  .introcenter p {
    font-size: 23px;
  }

  .aboutimg,
  .aboutdata {
    max-width: 350px;
    width: 90%;
  }

  .aboutimg img {
    width: 100%;
    height: 100%;
  }

  .aboutdata {
    padding-left: 10px;
  }

  .contact img,
  .mailbox {
    min-width: 365px;
  }

}